import { SearchBar } from 'antd-mobile'
import React from 'react'



const Search = (props) => {
  
  return (
      
      <div>
        <SearchBar
        placeholder="宝贝"
        showCancelButton
        maxLength = {8}
        onCancel = {()=>props.history.push('/home')}
        
      />
      <img 
      src='https://gw.alicdn.com/tfs/TB1M9eFxbH1gK0jSZFwXXc7aXXa-750-140.jpg' 
      alt='' 
      style = {{
        position : 'fixed',
        bottom : 0,
        width : '100%'
      }}
      />
      </div>
      
      
  )
}
export default Search
