import React from 'react'
import './home.css'
import Lunbo from './Lunbo'
import {connect} from 'react-redux'
import {getdata} from './Getdata';
function get(prop) { prop.dispatch(getdata())}
const Home = (props) => {
  
  get(props)
  const productarr = document.getElementsByClassName('product')
  console.log(productarr)
  //const imgs = document.getElementsByClassName('imgname')
  //console.log(imgs)  
  if(props.data == undefined){
        get(props)
  }else{
      console.log(props)
      console.log(props.data)
      function reset(datas){
            for(var i in datas){
                  console.log(datas[i])
                  let inner = `<img src='${datas[i].img}' />`+datas[i].title+''+'<br/>'+ datas[i].price 
                  productarr[i].innerHTML = inner
                  //console.log(imgs[i])
                  //imgs[i].src = datas[i].img
            }      
      }
      reset(props.data)
  }
  return (           
      <div>                 
            <header className='header' style={ {
                  position: 'fixed',
                  width: '100%',
                  top: 0
            } }>
                  <div className='header-bd'>
                      <a className='placeholder' onClick={()=>props.history.push('/home/search')}>
                            <span className = 'text iconfont icon-search'>
                            <i className='iconfont icon-2fangdajing'></i>  
                            寻找宝贝店铺
                            </span>
                       </a>
                  </div>
            </header>
  
      <div className='lunbo'>
            <Lunbo />    
      </div>

      <div id='products'>
           <div className="product">
                 <img src=''/>
                 
           </div>
           <div className="product">
                 <img src=''/>
                 
           </div>
           <div className="product">
                 <img src=''/>
                 
           </div>
           <div className="product">
                 <img src=''/>
                 
           </div>
           <div className="product">
                 <img src=''/>
                 
           </div>
           <div className="product">
                 <img src=''/>
                 
                 
           </div>
           <div className="product">
                 <img src=''/>
                 
           </div>
           <div className="product">
                 <img src=''/>
                             
           </div>
           <div className="product">
                 <img src=''/>
                 
                  
           </div>
           <div className="product">
                 <img src=''/>
                 
           </div>

      </div>
</div>

  )
}
const mapStateToProps = (state) =>({
    data :state.datalist.data
})
export default connect(mapStateToProps)(Home);


