import React from 'react';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import Mytab from './Mytab';
import Home from './Home';
import List from './List';
import Mine from './Mine';
import Productdetail from './Productdetail';
import Search from './Search';
import Shoucang from './Shoucang';
import Login from './Login';
import store from './store';
import { Provider } from 'react-redux';
const App = ()=>{
	
	return <Provider store = {store}>
		<Router>

<Switch>
	<Route exact path='/' component={Home} />
	if(!localStorage.getItem('userinfo')){
		<Route path='/login' component={Login} />
	}
	if(localStorage.getItem('userinfo')){
		<Route path='/shoucang' component={Shoucang} />
	}			
	<Route path='/list' component={List} />
	<Route path='/mine' component={Mine} />
	<Route path='/home/productdetail' component ={Productdetail} />
	<Route path='/home/search' component ={Search} />
	<Route render ={()=><Redirect to='/'/>}/>
</Switch>
<Mytab />
</Router>

	</Provider>
	
}
export default App;
