import { Carousel, WingBlank } from 'antd-mobile'
import React from 'react'
// import lunbo1 from '../assets/lunbo1.jpg'
// import lunbo2 from '../assets/lunbo2.jpg'
// import lunbo3 from '../assets/lunbo3.jpg'
// import lunbo4 from '../assets/lunbo4.jpg'
// import lunbo5 from '../assets/lunbo5.jpg'
// import lunbo6 from '../assets/lunbo6.jpg'
// import lunbo7 from '../assets/lunbo7.jpg'
// import lunbo8 from '../assets/lunbo8.jpg'

const lunbo1 = 'https://gw.alicdn.com/imgextra/i2/154/O1CN01ogznXF1D0Zco9ue7p_!!154-0-ppp-picassogw.jpg'
const lunbo2 = 'https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg'
const lunbo3 = 'https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg'
const lunbo4 = 'https://gw.alicdn.com/imgextra/i1/155/O1CN01a3VI6b1D121QmAx2f_!!155-0-lubanu.jpg'
const lunbo5 = 'https://gw.alicdn.com/imgextra/i4/185/O1CN01F8lCIp1DElu0kojjb_!!185-0-lubanu.jpg'
const lunbo6 = 'https://gw.alicdn.com/imgextra/i4/94/O1CN016EMBmd1CZ5rQwKF7U_!!94-0-lubanu.jpg'
const lunbo7 = 'https://img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i2/6000000005168/O1CN01RQ8EaH1o2zV1vITsc_!!6000000005168-0-octopus.jpg'
const lunbo8 = 'https://gw.alicdn.com/imgextra/i2/30/O1CN01oWlyPm1C5mVthWCaN_!!30-0-lubanu.jpg'
class Lunbo extends React.Component {

  state = {
    data: ['1', '2', '3','4','5','6','7','8'],
    imgHeight: 176,
  }
  componentDidMount() {
      setTimeout(() => {
        this.setState({
          data: [lunbo1, lunbo2, lunbo3, lunbo4, lunbo5,lunbo6, lunbo7, lunbo8],
        });
      }, 100)
  }

  render() {
    return (
      <WingBlank >
        <Carousel 
          autoplay={true}
          infinite
          //beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
          //afterChange={index => console.log('slide to', index)}
          style={{width:'385px' ,paddingLeft:'0%',marginLeft:'0%'}}
        >
          {this.state.data.map(val => (
              <img
                src={`${val}`}
                alt=""
                style={{ width: '375px' , verticalAlign: 'top' }}
                onLoad={() => {
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
          ))}
        </Carousel>
      </WingBlank>
    );
  }
}

export default Lunbo