import React from 'react';
import { TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        console.log(pathname.split('/'));
        if(pathname.split('/').length>2){
            return null;
        }

        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-shouye1'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-shouye'></i>                           
                        }
                        selected={ pathname === '/' }
                        onPress={ () => {
                            this.props.history.push('/');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-cart-'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-Cart-'></i>                           
                        }
                        title="收藏"
                        key="cart"
                        selected={ pathname === '/shoucang' }
                        onPress={ () => {
                            if(!localStorage.getItem('userinfo')){
                                this.props.history.push({pathname : '/login',state:this.props.location})
                            }else{
                                this.props.history.push('/shoucang')

                            }
                            
                        } }
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-dingdan'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-dingdan1'></i>                            
                        }
                        title="订单列表"
                        key="Friend"
                        selected={ pathname === '/list' }
                        onPress={ () => {
                            this.props.history.push('/list')

                        } }
                    >
                        
			</TabBar.Item>
                    <TabBar.Item
                        icon={
                        <i className='iconfont icon-wode'></i> 
                        }
                        selectedIcon={ 
                            <i className='iconfont icon-wode1'></i> 
                        }
                        title="我的"
                        key="my"
                        selected={ pathname === '/mine' }
                        onPress={ () => {
                            this.props.history.push('/mine')
                        } }
                    >                      
			</TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab);
